"use client";

import { useState } from "react";
import Link from "next/link";
import {
  MdSchedule,
  MdStar,
  MdCalendarToday,
  MdEditSquare,
  MdAttachMoney,
  MdTrendingUp,
} from "react-icons/md";
import { cn, formatCents } from "@/lib/utils";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { useTabStore } from "@/hooks/use-tab-store";

interface UserProfile {
  id: string;
  name: string | null;
  avatar: string | null;
  bio: string | null;
  createdAt: string;
  sellerProfile: {
    id: string;
    hourlyRate: number;
    tagline: string | null;
    averageRating: number | null;
    totalSessionsCompleted: number;
    totalEarnings: number;
  } | null;
}

interface ProfileStats {
  totalPurchased: number;
  totalSessionsAsBuyer: number;
  totalSpent: number;
}

interface ProfileTabProps {
  experienceId?: string;
  user: UserProfile | null;
  stats?: ProfileStats;
  isLoading?: boolean;
  standalone?: boolean; // When true, uses /app paths instead of /experiences
}

export function ProfileTab({
  experienceId,
  user,
  stats,
  isLoading,
  standalone,
}: ProfileTabProps) {
  const { setTab } = useTabStore();

  // Generate URLs based on context
  const settingsUrl = `/experiences/${experienceId}?tab=settings`;
  const becomeSellerUrl = standalone ? "/become-seller" : `/experiences/${experienceId}/become-seller`;
  const sellerDashboardUrl = standalone ? "/app?tab=dashboard" : `/experiences/${experienceId}?tab=dashboard&mode=seller`;

  if (isLoading || !user) {
    return <ProfileLoading />;
  }

  const isSeller = !!user.sellerProfile;
  const defaultStats: ProfileStats = stats || { totalPurchased: 0, totalSessionsAsBuyer: 0, totalSpent: 0 };

  return (
    <div className="p-4 sm:p-6 md:p-8">
      {/* Profile Header */}
      <Card className="mb-6">
        <CardContent className="p-6">
          <div className="flex flex-col sm:flex-row items-start gap-6">
            <Avatar className="h-20 w-20">
              <AvatarImage src={user.avatar || undefined} />
              <AvatarFallback className="text-2xl">
                {user.name?.[0]?.toUpperCase() || "?"}
              </AvatarFallback>
            </Avatar>

            <div className="flex-1">
              <div className="flex items-start justify-between gap-4">
                <div>
                  <h1 className="text-2xl font-semibold text-foreground">
                    {user.name || "Anonymous"}
                  </h1>
                  {user.sellerProfile?.tagline && (
                    <p className="text-muted-foreground mt-1">
                      {user.sellerProfile.tagline}
                    </p>
                  )}
                </div>
                {standalone ? (
                  <Button
                    variant="outline"
                    size="sm"
                    className="gap-2 hover:bg-muted hover:text-foreground"
                    onClick={() => setTab("settings")}
                  >
                    <MdEditSquare className="h-4 w-4" />
                    Edit Profile
                  </Button>
                ) : (
                  <Link href={settingsUrl}>
                    <Button variant="outline" size="sm" className="gap-2 hover:bg-muted hover:text-foreground">
                      <MdEditSquare className="h-4 w-4" />
                      Edit Profile
                    </Button>
                  </Link>
                )}
              </div>

              {user.bio && (
                <p className="text-muted-foreground mt-4">{user.bio}</p>
              )}

              <div className="flex flex-wrap gap-4 mt-4">
                {isSeller && (
                  <>
                    <div className="flex items-center gap-1.5">
                      <MdStar className="h-4 w-4 text-warning" />
                      <span className="text-sm font-medium">
                        {user.sellerProfile?.averageRating?.toFixed(1) || "N/A"}
                      </span>
                    </div>
                    <div className="flex items-center gap-1.5 text-muted-foreground">
                      <MdCalendarToday className="h-4 w-4" />
                      <span className="text-sm">
                        {user.sellerProfile?.totalSessionsCompleted || 0}{" "}
                        sessions
                      </span>
                    </div>
                    <Badge variant="secondary" className="gap-1">
                      <MdAttachMoney className="h-3 w-3" />
                      {formatCents(user.sellerProfile?.hourlyRate || 0)}/hr
                    </Badge>
                  </>
                )}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Stats */}
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-full bg-primary/10 flex items-center justify-center">
                <MdSchedule className="h-5 w-5 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Hours Purchased</p>
                <p className="text-2xl font-semibold text-foreground">
                  {defaultStats.totalPurchased / 2}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-full bg-muted flex items-center justify-center">
                <MdCalendarToday className="h-5 w-5 text-muted-foreground" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">
                  Sessions as Buyer
                </p>
                <p className="text-2xl font-semibold text-foreground">
                  {defaultStats.totalSessionsAsBuyer}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-full bg-success/10 flex items-center justify-center">
                <MdAttachMoney className="h-5 w-5 text-success" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Total Spent</p>
                <p className="text-2xl font-semibold text-foreground">
                  {formatCents(defaultStats.totalSpent)}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Seller Mode CTA */}
      {!isSeller && (
        <Card className="bg-gradient-to-r from-primary/10 to-primary/5 border-primary/20">
          <CardContent className="p-6">
            <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4">
              <div>
                <h3 className="text-lg font-semibold text-foreground">
                  Start Selling Your Time
                </h3>
                <p className="text-muted-foreground mt-1">
                  List your hours and earn money helping others in this
                  community.
                </p>
              </div>
              <Link href={becomeSellerUrl}>
                <Button className="gap-2">
                  <MdTrendingUp className="h-4 w-4" />
                  Become a Seller
                </Button>
              </Link>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Seller Stats (if seller) */}
      {isSeller && (
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Seller Stats</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 gap-4">
              <div className="p-4 rounded-lg bg-muted/50">
                <p className="text-sm text-muted-foreground">Total Earnings</p>
                <p className="text-xl font-semibold text-primary mt-1">
                  {formatCents(user.sellerProfile?.totalEarnings || 0)}
                </p>
              </div>
              <div className="p-4 rounded-lg bg-muted/50">
                <p className="text-sm text-muted-foreground">
                  Sessions Completed
                </p>
                <p className="text-xl font-semibold text-foreground mt-1">
                  {user.sellerProfile?.totalSessionsCompleted || 0}
                </p>
              </div>
            </div>
            <Link
              href={sellerDashboardUrl}
              className="block mt-4"
            >
              <Button variant="outline" className="w-full">
                Go to Seller Dashboard
              </Button>
            </Link>
          </CardContent>
        </Card>
      )}
    </div>
  );
}

function ProfileLoading() {
  return (
    <div className="p-4 sm:p-6 md:p-8">
      <Card className="mb-6">
        <CardContent className="p-6">
          <div className="flex items-start gap-6">
            <Skeleton className="h-20 w-20 rounded-full" />
            <div className="flex-1 space-y-3">
              <Skeleton className="h-8 w-48" />
              <Skeleton className="h-4 w-64" />
              <Skeleton className="h-16 w-full" />
            </div>
          </div>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
        {[...Array(3)].map((_, i) => (
          <Card key={i}>
            <CardContent className="p-6">
              <Skeleton className="h-16 w-full" />
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
